/* src/WorkExperience.css */
.work-experience {
    margin: 30px auto;
    padding: 30px;
    max-width: 800px;
    border-radius: 12px;
    background-color: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.work-experience:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.15);
}

.work-experience h2 {
    color: #2d3748;
    font-size: 2rem;
    margin-bottom: 25px;
    padding-bottom: 10px;
    border-bottom: 2px solid #e2e8f0;
}

.experience {
    margin-bottom: 25px;
    padding: 20px;
    border-radius: 8px;
    background-color: #f8fafc;
    transition: background-color 0.3s ease;
}

.experience:not(:last-child) {
    margin-bottom: 30px;
}

.experience:hover {
    background-color: #f1f5f9;
}

.experience h3 {
    color: #1e40af;
    font-size: 1.3rem;
    margin: 0 0 8px 0;
    font-weight: 600;
}

.experience .company {
    color: #4a5568;
    font-size: 1.1rem;
    font-weight: 500;
    display: block;
    margin-bottom: 12px;
}

.experience .duration {
    color: #718096;
    font-size: 0.9rem;
    margin-bottom: 15px;
    display: inline-block;
    padding: 4px 8px;
    background-color: #e2e8f0;
    border-radius: 4px;
}

.experience ul {
    list-style-type: none;
    padding-left: 20px;
    margin: 15px 0 0 0;
}

.experience li {
    color: #4a5568;
    margin-bottom: 8px;
    line-height: 1.6;
    position: relative;
    padding-left: 20px;
}

.experience li:before {
    content: "▹";
    color: #1e40af;
    position: absolute;
    left: 0;
    top: 1px;
}

@media (max-width: 768px) {
    .work-experience {
        margin: 20px;
        padding: 0px;
    }
    
    .experience {
        padding: 15px;
    }
}