/* src/AboutMe.css */
.about-me {
    padding: 0px 20px;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  .section-title {
    text-align: center;
    color: #023047;
    margin-bottom: 40px;
    font-size: 1.5rem;
    position: relative;
  }
  
  .section-title::after {
    content: '';
    display: block;
    width: 60px;
    height: 4px;
    background: #FFB703;
    margin: 15px auto;
    border-radius: 2px;
  }
  
  .about-content {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    justify-content: space-between;
  }
  
  .about-text {
    flex: 1;
    min-width: 300px;
  }
  
  .about-text p {
    margin-bottom: 20px;
    line-height: 1.6;
    color: #333;
    font-size: 1.1rem;
  }
  
  .skills-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 15px;
    margin: 25px 0;
    padding-left: 20px;
  }
  
  .skills-list li {
    margin-bottom: 10px;
    position: relative;
    padding-left: 25px;
    line-height: 1.5;
  }
  
  .skills-list li::before {
    content: '▹';
    position: absolute;
    left: 0;
    color: #FFB703;
  }
  
  .about-contacts {
    background: #023047;
    padding: 30px;
    border-radius: 8px;
    color: white;
    min-width: 280px;
    align-self: flex-start;
  }
  
  .about-contacts h3 {
    margin-top: 0;
    margin-bottom: 20px;
    font-size: 1.5rem;
    color: #FFB703;
  }
  
  .about-contacts ul {
    list-style: none;
    padding: 0;
  }
  
  .about-contacts li {
    margin-bottom: 15px;
    display: flex;
    align-items: center;
  }
  
  .about-contacts a {
    color: #8ECAE6;
    text-decoration: none;
    transition: color 0.3s;
  }
  
  .about-contacts a:hover {
    color: #FFB703;
  }
  
  @media (max-width: 768px) {
    .about-content {
      flex-direction: column;
    }
    
    .about-contacts {
      width: 100%;
    }
    
    .section-title {
      font-size: 2rem;
    }
  }