/* src/Skills.css */
.skills-section {
    padding: 0px 20px;
    max-width: 1200px;
    margin: 0 auto;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  .section-title {
    text-align: center;
    color: #023047;
    margin-bottom: 40px;
    font-size: 2.5rem;
    position: relative;
  }
  
  .section-title::after {
    content: '';
    display: block;
    width: 80px;
    height: 4px;
    background: #FFB703;
    margin: 15px auto;
    border-radius: 2px;
  }
  
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 30px;
    margin-bottom: 50px;
  }
  
  .skill-card {
    background: white;
    border-radius: 10px;
    padding: 25px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
  }
  
  .skill-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
  
  .skill-card h3 {
    color: #023047;
    margin-top: 0;
    margin-bottom: 20px;
    text-align: center;
  }
  
  .chart-container {
    height: 300px;
    position: relative;
  }
  
  .skills-list-section {
    background: white;
    border-radius: 10px;
    padding: 30px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .skills-list-section h3 {
    color: #023047;
    text-align: center;
    margin-bottom: 30px;
    font-size: 1.8rem;
  }
  
  .skills-categories {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 30px;
  }
  
  .category {
    background: #f0f8ff;
    padding: 20px;
    border-radius: 8px;
    border-left: 4px solid #FFB703;
  }
  
  .category h4 {
    color: #023047;
    margin-top: 0;
    margin-bottom: 15px;
  }
  
  .category ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .category li {
    margin-bottom: 10px;
    padding-left: 20px;
    position: relative;
    line-height: 1.5;
  }
  
  .category li::before {
    content: '•';
    position: absolute;
    left: 0;
    color: #FFB703;
    font-weight: bold;
  }
  
  @media (max-width: 768px) {
    .skills-grid {
      grid-template-columns: 1fr;
    }
    
    .section-title {
      font-size: 2rem;
    }
    
    .skills-categories {
      grid-template-columns: 1fr;
    }
  }